import { environment } from 'src/environments/environment';
import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { FOPModel } from 'src/models/fop/fop.model';
import { AlertService } from 'src/services/alert.service';
import { SharedComponents } from '../../shared.components';
import { IUser } from 'src/interfaces/i.user.interface';
import { UserService } from 'src/services/user.service';
import { BranchesEnum } from 'src/enums/branches.enum';

@Component({
  selector: 'app-users',
  templateUrl: './users.component.html',
  styles: [
  ]
})
export class UsersComponent extends SharedComponents implements OnInit {

  skip: number = 0;
  limit: number = 30;
  entries: IUser[] = [];
  fop: FOPModel = new FOPModel();
  fopSearch: string;
  userFilterForm: FormGroup = new FormGroup({});
  env = environment;
  constructor(public service: UserService) { super(); }

  ngOnInit(): void {
    this.service.seoService.updateSEO("users");
    //Init Form Group
    this.userFilterForm = new FormGroup({
      userName: new FormControl(null),
      email: new FormControl(null),
      createdDate: new FormControl(null),
      languageCode: new FormControl(null),
      branchIndex: new FormControl(BranchesEnum.Main)
    });
    //Get My Users Now
    this.getUsers();

//On Deleted User
    this.allSubscriptions.push(
      this.service.onDeletedItem.subscribe(userDeletedId=>{
        this.entries.splice(this.entries.findIndex(c=> c._id==userDeletedId),1)
      })
    )
  }

  /**
   * Get My Users
   */
  getUsers(isFilter: boolean = false): void {
    this.fop.paging.isRequestingToServer = true;
    this.userFilterForm.disable();
    if (isFilter) {
      this.entries = [];
      this.skip = 0;
      this.fop = new FOPModel(this.fop.fieldNames, this.entries.length);
    }

    //Get Users Now From Server
    this.allSubscriptions.push(
      this.service.http.getUsers<IUser[]>(this.skip, this.limit, this.userFilterForm.value).subscribe(res => {
        //Enable Filter Form Now
        this.userFilterForm.enable();
        //Update FOP Properties
        if (res.noMoreOfResult)
          this.fop.paging.disableGetMoreButton = true;
        this.fop.paging.isRequestingToServer = false;


        if (res.isSuccess) {
          this.entries = [...this.entries, ...res.result];

          //Fop 
          this.fop = new FOPModel(this.fop.fieldNames, this.entries.length);
          this.skip += this.limit;
        } else {
          AlertService.showResponseMessage(res);
        }
        this.fop.paging.isRequestingToServer = false;
        this.userFilterForm.enable();
      }, error => {
        AlertService.canRequestError(error);
        this.fop.paging.isRequestingToServer = false;
        this.userFilterForm.enable();
      },
        () => {

        }));
  }


  funTrack(index: number, user: IUser): string | undefined {
    return user?._id;
  }

  /**Reset Form Filter And Get Users Affter Reset */
  resetFilter(): void {
    this.userFilterForm.reset();
    this.getUsers(true);
  }




}//End Class
