import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { SharedComponents } from 'src/app/shared.components';
import { AlertService } from 'src/services/alert.service';
import { UserService } from 'src/services/user.service';
import { ITransaction } from 'src/interfaces/i.transaction.interface';
import { IUser } from 'src/interfaces/i.user.interface';

@Component({
  selector: 'app-delete-user',
  templateUrl: './delete.user.component.html',
  styles: [
  ]
})
export class DeleteUserComponent extends SharedComponents implements OnInit {

  constructor(public service: UserService, private activateRouter: ActivatedRoute) { super() }
  ngOnInit(): void {
    this.service.initDeleteForm()
    this.service.deleteingForm.controls._id.setValue(this.activateRouter.snapshot.params.targetId);

    //Check If Must Be Get Object From The Server
    if (history.state.deletingRefranceNumber) {
      this.service.deleteingForm.controls.name.setValue(history.state.deletingUserName);
      history.state.deletingUserName = null;
    }
    else {
      //Get From The Serer
      this.getUserToDelete();
    }
  }

  /**
   * Get Car Mark From Server To Update
   */
  getUserToDelete() {
    this.service.isRequestingToServer = true;
    this.allSubscriptions.push(
      this.service.http.getUserToDelete<IUser>(this.activateRouter.snapshot.params.targetId)
        .subscribe(res => {
          this.service.isRequestingToServer = false;
          if (res.isSuccess) {
            this.service.deleteingForm.controls.name.setValue(res.result.name);
          } else
            AlertService.showResponseMessage(res);

        }, error => {
          this.service.isRequestingToServer = false;
          AlertService.canRequestError(error);
        }));
  }

  /**
   * Delete Item
   */
  onSubmit() {
    if (this.service.deleteingForm.invalid) {
      this.service.isFormSubmitError = true;
      return;
    }
    this.service.isRequestingToServer = true;
    this.allSubscriptions.push(
      this.service.http.deleteUser<ITransaction>(this.activateRouter.snapshot.params.targetId)
        .subscribe(res => {
          AlertService.showResponseMessage(res);
          this.service.isRequestingToServer = false;
          if (res.isSuccess) {
            //Pass The New Item To Page
            this.service.onDeletedItem.emit(this.activateRouter.snapshot.params.targetId);
            //Back To Component
            this.service.closeModel();
          }
        }, error => {
          this.service.isRequestingToServer = false;
          AlertService.canRequestError(error);
        }));
  }


}//End Class
