import { EventEmitter, Injectable } from "@angular/core";
import { FormControl, FormGroup, Validators } from "@angular/forms";
import { Router } from "@angular/router";
import { BranchesArray } from "src/consts/branches.const";
import { ClinetTypesArray } from "src/consts/clinet.types.const";
import { RolesArray } from "src/consts/roles.const";
import { BranchesEnum } from "src/enums/branches.enum";
import { ClinetTypeEnum } from "src/enums/clinet.type.enum";
import { RolesEnum } from "src/enums/roles.enum";
import { environment } from "src/environments/environment.prod";
import { FileDragHandle } from "src/interfaces/file.drag.handel.interface";
import { IUser } from "src/interfaces/i.user.interface";
import { CurrentUserService } from "./authentcation/current.user.service";
import { CustomValidatorsService } from "./custom.validators.service";
import { HttpService } from "./http/http.service";
import { SEOService } from "./seo.service";
import { SweetAlertService } from "./sweet.alert.service";

@Injectable()
export class UserService {
    branches = BranchesArray;
    roles = RolesArray;
    clinetTypes = ClinetTypesArray;

    form: FormGroup = new FormGroup({});
    deleteingForm: FormGroup = new FormGroup({});

    formSubmitError: boolean;
    userInfo = {} as IUser;
    newPicture = {} as FileDragHandle;
    defaultImageUrl = `${environment.api_url}files/users/default.png`;
    pageTitle: string = "createANewClinet";
    isShowClinetTypes: boolean = true;
    isRequestingToServer: boolean = false;
    isFormSubmitError: boolean = false;
    onDeletedItem: EventEmitter<string> = new EventEmitter<string>();

    constructor(public u: CurrentUserService, public seoService: SEOService, public http: HttpService, public router: Router, public sweetAlertService: SweetAlertService) {

    }

    /** Init Create Update Form */
    initForm(isUpdateForm = false) {
        let password = isUpdateForm ?
            new FormControl(null, [Validators.maxLength(50), Validators.minLength(5)])
            : new FormControl(null, [Validators.required, Validators.maxLength(50), Validators.minLength(5)]);

        this.form = new FormGroup({
            _id: new FormControl(),
            userName: new FormControl(null, [Validators.required, CustomValidatorsService.userName]),
            fullName: new FormControl(null, Validators.maxLength(100)),
            phoneNumber: new FormControl(null, CustomValidatorsService.phoneNumber),
            password: password,
            //بما ان موقتا لا يوجد سوىء الزيائن فاننا ننشىء المستخدم الجديد كـ زبون فقط
            roleId: new FormControl(RolesEnum.Clinet, Validators.required),
            clinetTypeId: new FormControl(ClinetTypeEnum.personal),
            branchIndex: new FormControl(BranchesEnum.Main, Validators.required),
        });
        this.form.controls.roleId.valueChanges.subscribe(value => this.checkIsAlloeShowClinetTypes())
    }

    /**
     * Init Delete Form
     */
    initDeleteForm() {
        this.deleteingForm = new FormGroup({
            _id:new FormControl(null,Validators.required),
            name: new FormControl(null,Validators.required)
        });
        this.deleteingForm.controls.name.disable();
    }


    /** Check If Allow Show Clinet Type */
    checkIsAlloeShowClinetTypes() {
        if (+this.form.controls.roleId.value == RolesEnum.Clinet) {
            this.isShowClinetTypes = true;
        }
        else {
            this.form.controls.clinetTypeId.setValue(null);
            this.isShowClinetTypes = false;
        }
    }


    /** Show And Hide Password Toggel */
    showPasswordToggal(elementTrget: HTMLInputElement) {
        if (elementTrget.type == "password")
            elementTrget.type = "text";
        else
            elementTrget.type = "password";
    }

    /** Close Thihs Model And Back To Users */
    closeModel() {
        this.router.navigate(['/users']);
    }
}//End Class