import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { CreateUserComponent } from "src/app/back-end/users/create-user/create.user.component";
import { DeleteUserComponent } from "src/app/back-end/users/delete-user/delete.user.component";
import { EditUserComponent } from "src/app/back-end/users/edit-user/edit.user.component";
import { UsersComponent } from "src/app/back-end/users/users.component";

 
const ROUTS: Routes = [
    {
        path: '', component: UsersComponent,
        children: [
            { path: 'delete/:targetId', component: DeleteUserComponent },
        ]
    },
    { path: 'create', component: CreateUserComponent },
    { path: 'edit/:userId', component: EditUserComponent },

];

@NgModule({
    imports: [RouterModule.forChild(ROUTS)],
    exports: [RouterModule]
})
export class UsersRoutingModule {

}

